body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.logo {
    height: 100px;
    width: 100px;
    border-radius: 50%;
    margin: 0 auto;
}

.landingPage {
    border-top: 100px solid #3080eb;
    /* border-bottom: 100px solid #3080eb; */
    min-height: 100vh;
}

.contentContainer {
    margin-top: 10vh;
}

.contentContainer h1 {
    padding: 0 10%;
    font-size: 3rem;
}

.contentContainer p {
    padding: 0 10%;
    font-family: 'Lato', sans-serif;
    font-size: 1rem;
}

.inputName {
    border-radius: 25px;
    border: 2px solid #3080eb;
    padding: 10px 20px;
    margin-top: 20px;
    text-align: center;
}

.inputName::-webkit-input-placeholder {
    font-size: .8rem;
}

.inputName::-ms-input-placeholder {
    font-size: .8rem;
}

.inputName::placeholder {
    font-size: .8rem;
}

.startButton {
    display: block;
    background-color: #3080eb;
    color: white;
    width: 100px;
    margin: 20px auto 10vh auto;
    border-radius: 25px;
    padding: 10px 0;
    text-decoration: none;
    box-shadow: 3px 5px 3px 0 grey;
    -webkit-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0);
    transition: -webkit-transform 0.25s ease-in-out;
    transition: transform 0.25s ease-in-out;
    transition: transform 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;
}

.startButton:active {
    -webkit-transform: translateX(3px) translateY(3px);
            transform: translateX(3px) translateY(3px);
}

footer {
    position: fixed;
    width: 100%;
    bottom: 0;
    left: 0;
    font-size: 2rem;
    background-color: #3080eb;
    text-align: center;
}

footer i {
    padding: 10px;
}

footer a {
    /* margin: 30px 0; */
    /* padding: 20px 0; */
    float: left;
    text-transform: uppercase;
    text-decoration: none;
    color: white;
}

footer p {
    font-size: 1rem;
}

.homeButton{
    margin: 0;
    padding: 13px 0;
    width: calc(100% / 3);
    transition: background .2s ease;
}

.homeButton:hover {
    background: #3e6da7;
}

.helpButton {
    color: white;
    background-color: transparent;
    border: none;
}

@media (max-width: 970px) {
    .contentContainer h1 {
        font-size: 2.5rem;
    }

    .contentContainer p {
        font-size: 1rem;
    }

}
@media (max-width: 600px) {
    .homeButton i{
        font-size: 1.2rem;
    }
}

@media (max-width: 510px) {
    footer {
        font-size: 1rem;
    }

    footer p {
        font-size: .5rem;
    }

    .contentContainer h1 {
        font-size: 2rem;
    }

    .contentContainer p {
        font-size: 1rem;
    }
}
@media (max-height: 700px) and (max-width: 340px) {
    .contentContainer h1 {
        font-size: 1.7rem;
        line-height: 1.8rem;
        margin-bottom: 10px;
    }
    .logo {
        height: 80px;
        width: 80px;
        margin-bottom: 10px;
    }
}

@media (max-height: 750px) and (max-width: 780px) {
    .contentContainer {
        margin-top: 3vh;
    }
}

@media (max-height: 685px) {
    .contentContainer h1 {
        font-size: 1.7rem;
        line-height: 1.8rem;
    }
    .logo {
        height: 60px;
        width: 60px;
        margin-bottom: 10px;
    }

}

@media (max-height: 600px) {
    /* .contentContainer h1 {
        font-size: 2rem;
    } */
    .logo {
        height: 60px;
        width: 60px;
    }

    .contentContainer {
        margin-top: 5vh;
    }
    .contentContainer h1 {
        font-size: 1.4rem;
    }
    .contentContainer p {
        font-size: .9rem;
    }

    .inputName, .startButton {
        padding: 8px 0;
        margin-top: 10px;
    }

    footer a {
        margin: 10px 0;
    }

    

    .landingPage {
        border-top: 30px solid #3080eb;
    }


}
