.modalWrapper {
    border-radius: 25px;
    border: 2px solid #3080eb;
    background: white;
    width: 90%;
    height: 55vh;
    position: absolute;
    margin-left: 5%;
    margin-top: 50px;
    transition: all .8s;
    z-index: 999;
}

.modalHeader {
    height: 40px;
    padding: 5px 20px;
    margin-bottom: 10px;
    text-align: center;
}

.modalHeader h3 {
    color: black;
    float: left;
    margin: 0;
    padding: 10px 0;
    font-size: 2rem;
    text-align: center;
    width: 100%;
}

.modalBody {
    padding: 10px 30px;
    text-align: center;
}

.modalBody li {
    list-style: auto inside;
    padding: 10px 0;
}

.closeModalBtn {
    color: black;
    cursor: pointer;
    font-size: 30px;
    margin: 10px 20px 0 0;
    position: absolute;
    top: 0;
    right: 0;
}

.backDrop {
    height: 100%;
    position: fixed;
    transition: all 1.3s;
    width: 100%;
}

@media (max-width: 1050px) {
    .modalWrapper {
        width: 70%;
        height: 65vh;
        margin-left: 15%;
    }
}

@media (max-width: 770px) {
    .modalWrapper {
        width: 80%;
        margin-left: 10%;
        margin-top: 0;
    }
}

@media (max-width: 620px) {
    .modalWrapper {
        width: 95%;
        margin-left: 2.5%;
    }

    .modalHeader h3 {
        font-size: 1.2rem;
    }

    .modalBody {
        padding: 5px 15px;
    }

    .modalBody p {
        display: none;
    }

    .modalBody li {
        padding: 3px 0;
    }
}

@media (max-height: 710px) {
    .modalHeader {
        height: 30px;
    }
    .modalWrapper {
        margin-top: 5px;
    }
}

@media (max-height: 550px) {
    .modalHeader h3 {
        font-size: 1.6rem;
    }

    .modalWrapper {
        margin-top: 5px;
    }

    .modalBody {
        font-size: 0.8rem;
    }
}

.gamePage {
    width: 75%;
    float: left;
    border-top: 80px solid #3080eb;
    height: 100vh;
}

.movieCatalogue {
    overflow: scroll;
    width: 90%;
    padding: 20px;
    height: 70%;
    margin: 30px auto 0 auto;
}

.movieCatalogueMovie {
    min-width: 25%;
    width: 200px;
    float: left;
    height: 300px;
    position: relative;
    padding: 20px;
    object-fit: cover;
    overflow: hidden;
    margin-bottom: 2%;
}

.overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    word-wrap: break-word;
    padding: 50px 40px;
    font-weight: bold;
    font-size: 1.2rem;
    background: linear-gradient(rgba(255,255,255,0.6), rgba(255,255,255,0.7));
    opacity: 0;
    transition: opacity 0.25s ease-in-out;
}

.movieCatalogueMovie:hover .overlay {
    opacity: 1;
}

.movieCatalogueMovie button {
    border: none;
    height: 40px;
    width: 40px;
    border-radius: 30px;
    text-align: center;
    background-color: #c0f3fb;
    font-weight: bold;
    position: absolute;
    top: 0;
    right: 0;
}

.instructions {
    font-weight: bold;
    padding: 10px;
}

.yearDropDown {
    width: 90%;
    padding: 10px;
    text-align: center;
    font-weight: bold;
    border: none;
    background: url(data:image/png;base64,) 95% no-repeat; /* image came from Google images */
    background-color:#c0f3fb;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    cursor: pointer;
    /* transition: all .5s ease; */
}

.yearDropDown option {
    margin: 0 auto;
    background: #c0f3fb;
    color: black;
}

.gameFooter {
    position: fixed;
    width: 75%;
    bottom: 0;
    font-size: 1.7rem;
    background-color: #3080eb;
    text-align: center;
}

/* .gameFooter a {
    margin: 10px 0;
} */

