    float: left;
    background: #c0f3fb;
    z-index: -10;
}

.currentList {
    margin: 0 auto;
    height: 55%;
    width: 80%;
    position: absolute;
    top: 0; 
    right: 0; 
    left: 0;
    bottom: 0;
    overflow: scroll;
    z-index: -1;
    -webkit-transform: translateY(60%);
            transform: translateY(60%);
    background-image: linear-gradient(to bottom, rgba(0,0,0,0), rgba(115, 181, 201, .2));
    border-radius: 20px;
    /* border: 1px solid yellow; */
